import { createRouter, createWebHistory } from '@ionic/vue-router';
import { RouteRecordRaw } from 'vue-router';
import HomePage from '../views/HomePage.vue'
import { Client } from '@/composables/client';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'Home',
    component: HomePage,
    meta: {
      requiresAuthentication: true
    }
  },
  {
    path: '/tests',
    component: () => import('@/views/TestsPage.vue'),
    meta: {
      requiresAuthentication: true
    }
  },
  {
    path: '/patient-details/:id',
    component: () => import('@/views/PatientDetails.vue'),
    meta: {
      requiresAuthentication: true
    }
  },
  {
    path: '/test/details',
    component: () => import('@/views/TestDetails.vue'),
    meta: {
      requiresAuthentication: true
    }
  },
  {
    path: '/test/reject/:id',
    component: () => import('@/views/RejectTest.vue'),
    meta: {
      requiresAuthentication: true
    }
  },
  {
    path: '/order/view/:identifier',
    component: () => import('@/views/IdentifierResult.vue'),
    meta: {
      requiresAuthentication: true
    }
  },
  {
    path: '/find',
    component: () => import('@/views/FindOrRegister.vue'),
    meta: {
      requiresAuthentication: true
    }
  },
  {
    path: '/edit-client/:client_id',
    component: () => import('@/views/EditClient.vue'),
    meta: {
      requiresAuthentication: true
    }
  },
  {
    path: '/order/:client_id',
    component: () => import('@/views/NewOrder.vue'),
    meta: {
      requiresAuthentication: true
    }
  },
  {
    path: '/login',
    component: () => import('@/views/LoginPage.vue'),
    meta: {
      requiresAuthentication: false
    }
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  if (!to?.meta) return next()
  if ((typeof to.meta.requiresAuthentication === 'boolean' &&
    to.meta.requiresAuthentication) && !Client().hasSession.value) {
      return next('/login')
  } else {
    next()
  }
})

export default router
