<template>
  <ion-page>
    <ion-header :translucent="true">
      <ion-toolbar style="padding: 10px;">
        <ion-img slot="start" style="height: 60px;" src="assets/Malawi-Coat_of_arms_of_arms.png"></ion-img>
        <ion-title style="text-transform: uppercase;" class="his-lg-text">IBLIS Reception</ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-card style="margin:10px;">
        <barcode-input
          placeholder="Scan Accession Number"
          @onScan="onScan">
        </barcode-input>
      </ion-card>
      <ion-card cstyle="margin:10px auto;margin-top: 3%;width:90%">
        <ion-list>
          <ion-item lines="none" class="ion-padding his-lg-text">
            <ion-label>Full name:</ion-label>
            <ion-label slot="end"><b>{{ fullName }}</b></ion-label>
          </ion-item>
          <ion-item lines="none" class="ion-padding his-lg-text">
            <ion-label>Department:</ion-label>
            <ion-label slot="end"><b>{{ department }}</b></ion-label>
          </ion-item>
          <ion-item lines="none" class="ion-padding his-lg-text">
            <ion-label>Gender:</ion-label>
            <ion-label slot="end"><b>{{ gender }}</b></ion-label>
          </ion-item>
        </ion-list>
      </ion-card>
    </ion-content>
    <ion-footer>
      <ion-toolbar color="light" style="padding: 10px;">
        <ion-button
          @click="signout"
          size="large"
          color="danger">
          Logout
        </ion-button>
        <ion-button
          @click="() => router.push('/tests')"
          slot="end"
          size="large"
          >
          Tests
        </ion-button>
        <ion-button
          @click="router.push('/find')"
          slot="end"
          size="large"
          color="success">
          Find/Register
        </ion-button>
      </ion-toolbar>
    </ion-footer>
  </ion-page>
</template>

<script setup lang="ts">
import {
  IonContent,
  IonButton,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonFooter,
  IonList,
  IonItem,
  IonLabel,
  IonAvatar,
  IonImg
} from '@ionic/vue';
import { computed } from 'vue';
import { Client } from "@/composables/client"
import BarcodeInput from "@/components/BarcodeInput.vue"
import router from "@/router"
import Orders from "@/composables/orders"

const { signout, token }  = Client()

const data = token.value?.authorization?.user

const fullName = computed(() => {
  return `${data?.first_name||''} ${data?.last_name||''}`
})

const department = computed(() => {
  return `${(data?.departments||[]).map((d: any) => d.name).splice(0, 3).join(', ')}...`
})

const  gender = computed(() => {
  return `${data?.sex === 'M' ? 'Male' : data?.sex === 'F' ? 'Female' : 'Unknown'}`
})

const onScan = async (text: string) => {

  const client = Client()

  const res = await client.getJson('global');

  let param = ''

  const siteCode = res?.data.code

  if(text.startsWith('X')){
    param = text
  }else{
    param = `${siteCode}${text}`
  }

  Orders().searchbyID(param)
}
</script>
