import 'nprogress/nprogress.css'
import { ref, watch } from "vue"
import router from "@/router"
import storage from "./storage"
import nprogress from 'nprogress'
import { alertConfirmation, toastDanger, toastSuccess, toastWarning } from '@/utils/Alerts'

const token = ref<any>({})
const host = ref<string>('')
const isOnline = ref(true)
const hasSession = ref(false)
const HEALTH_CHECK_INTERVAL = 6666
let healthCheckInterval: null|number = null

export interface ClientRequestParamInterface {
    method: 'POST' | 'GET' | 'PUT';
    mode?: string;
    body?: string;
}

export interface AunthenticationParamInterface {
    username: string;
    password: string;
    department: string;
}

export enum ClientError {
    CONFIGURATION_ERROR = 'Unable to resolve configuration',
    ENTITY_ERROR = 'Entity Error',
    NOT_FOUND = 'Request not found',
    REQUEST_FAILED = 'Request Failed',
    NETWORK_ERROR = 'Network Error',
    SERVER_ERROR = 'Server Error',
    GENERAL_ERROR = 'General Error',
    AUTHENTICATION_ERROR = 'Authentication Error',
}

export enum ClientStorageKey {
    AuthToken='auth_token'
}

export function Client(baseUrl='/api/v1/') {
    const request = async (url: string, params: ClientRequestParamInterface) => {
        const headers = {
            'Authorization': `${token.value?.authorization?.token}`,
            'Content-Type': 'application/json'
        }
        if (!host.value) {
            const req = await fetch('/config.json')
            if (req.ok) {
                const res = await req.json()
                if (isObj(res)) host.value = res.host
            }
        }
        const expandedUrl = `${host.value}${baseUrl}${url}`
        return fetch(expandedUrl, { ...params, headers, mode: 'cors' } as any)
    }

    const isObj = (data: any) =>  typeof data === 'object' && data != null

    const isNetworkError = (strException: string) => /NetworkError|Failed to fetch/i.test(strException)

    const hasInternet = async () => {
        try {
            const req = await fetch('https://dns.google.com/resolve?name=example.com')
            return req.ok
        } catch (e) {
            return false
        }
    }

    const handleJsonResponse = async (req: any) => {
        try {
            const res = await req
            if ([200, 201].includes(res?.status || 0)) {
                const data = await res?.json()
                if (isObj(data)) {
                    return { ok: true, data }
                } else {
                    return { ok: true }
                }
            }

            if (res.status === 400) {
                const data = await res.json()
                if (isObj(data) && (data.errors || data.message)) {
                    return { ok: false, error: data.errors || data.message }
                } else {
                    return { ok: false, error: ClientError.REQUEST_FAILED }
                }
            }

            if (res.status === 401) {
                clearThisSession()
                return { ok: false, error: ClientError.AUTHENTICATION_ERROR }
            }

            if (res.status === 422) {
                return { ok: false, error: ClientError.ENTITY_ERROR }
            }

            if (res.status === 404) {
                return { ok: false, error: ClientError.NOT_FOUND }
            }

            if (res.status === 500) {
                return { ok: false, error: ClientError.SERVER_ERROR }
            }
        } catch (e) {
            if (isNetworkError(`${e}`)) {
                toastDanger('Network Error has occurred', 5000)
                isOnline.value = false
                return { ok: false, error: ClientError.NETWORK_ERROR }
            }
            return { ok: false, error: ClientError.GENERAL_ERROR }
        }
        isOnline.value = true
    }

    const handleTextResponse = async (req: any) => {
        try {
          const res = await req;

          if ([200, 201, 304].includes(res?.status || 0)) {
            const data = await res?.text();
            if (data) {
              return { ok: true, data };
            } else {
              return { ok: true };
            }
          }

          if (res.status === 400) {
            const data = await res.text();
            if (data) {
              return { ok: false, error: data };
            } else {
              return { ok: false, error: ClientError.REQUEST_FAILED };
            }
          }

          if (res.status === 401) {
            clearThisSession();
            return { ok: false, error: ClientError.AUTHENTICATION_ERROR };
          }

          if (res.status === 422) {
            return { ok: false, error: ClientError.ENTITY_ERROR };
          }

          if (res.status === 404) {
            return { ok: false, error: ClientError.NOT_FOUND };
          }

          if (res.status === 500) {
            return { ok: false, error: ClientError.SERVER_ERROR };
          }
        } catch (e) {
          if (isNetworkError(`${e}`)) {
            toastDanger('Network Error has occurred', 5000);
            isOnline.value = false;
            return { ok: false, error: ClientError.NETWORK_ERROR };
          }
          return { ok: false, error: ClientError.GENERAL_ERROR };
        }
        isOnline.value = true;
      };


    const getJson = async (endpoint: string, params={} as Record<string, string|number>) => {
        const strParams = Object.keys(params).map(k => `${k}=${params[k]}`).join('&')
        let url = endpoint
        if (strParams) url += `?${strParams}`
        nprogress.start()
        const response = await handleJsonResponse(
            request(url, { method: 'GET'})
        )
        nprogress.done()
        return response
    }

    const getText = async (endpoint: string, params={} as Record<string, string|number>) => {
        const strParams = Object.keys(params).map(k => `${k}=${params[k]}`).join('&')
        let url = endpoint
        if (strParams) url += `?${strParams}`
        nprogress.start()
        const response = await handleTextResponse(
            request(url, { method: 'GET'})
        )
        nprogress.done()
        return response
    }

    const putJson = async (endpoint: string, data: any) => {
        nprogress.start()
        const response = await handleJsonResponse(
            request(endpoint, { method: "PUT", body: JSON.stringify(data)})
        )
        nprogress.done()
        return response
    }

    const postJson = async (endpoint: string, data: any) => {
        nprogress.start()
        const response = await handleJsonResponse(
            request(endpoint, { method: "POST", body: JSON.stringify(data)})
        )
        nprogress.done()
        return response
    }

    const authenticate = async (data: AunthenticationParamInterface) => {
        const req = await postJson('auth/login', data)
        if (req?.ok) {
            startSession(req.data)
            return true
        }
        if (req?.error === ClientError.AUTHENTICATION_ERROR) {
            clearThisSession()
            toastWarning("Unable to login, verify your username and password")
            return false
        }
        return false
    }

    const startSession = (tokenData: any) => {
        if (tokenData) {
            storage.set(ClientStorageKey.AuthToken, tokenData)
            token.value = tokenData
            hasSession.value = true
            toastSuccess(`Welcome ${token.value?.authorization?.user?.username || 'User'}`)
        } else {
            token.value = {}
            hasSession.value = false
        }
    }

    const clearThisSession = () => {
        token.value = {}
        hasSession.value = false
        storage.del(ClientStorageKey.AuthToken)
        router.push('/login')
    }

    const signout = () => {
        alertConfirmation("Are you sure you want to logout?")
            .then((ok) => ok && clearThisSession())
    }

    const pingApi = () => request('auth/login', { method: 'POST'})
        .then(req => isOnline.value = [401, 200, 201].includes(req?.status))

    watch(isOnline, (online) => {
        if (online) {
            toastSuccess('Now back online')
            if (healthCheckInterval) {
                clearInterval(healthCheckInterval)
                healthCheckInterval = null
            }
        } else {
            if (!healthCheckInterval) {
                healthCheckInterval = setInterval(() => pingApi(), HEALTH_CHECK_INTERVAL)
            }
        }
    })

    /**
     * Validate existing token for authentication
     */
    if (!Object.keys(token.value).length) {
        const cachedToken = storage.get(ClientStorageKey.AuthToken)
        if (cachedToken) startSession(cachedToken)
    }

    /**
     * Read configuration file for host configuration
     */
    if (!host.value) {
        fetch('/config.json').then((res) => {
            if (!res) return toastDanger(ClientError.CONFIGURATION_ERROR)
            res.json().then((data) => {
                if (isObj(data)) host.value = data.host
            })
        }).catch((e) => {
            toastDanger(ClientError.CONFIGURATION_ERROR)
            console.error(e)
        })
    }

    return {
        token,
        isOnline,
        hasSession,
        putJson,
        signout,
        getJson,
        getText,
        postJson,
        hasInternet,
        authenticate
    }
}
