import router from "@/router"
import { toastDanger, toastWarning } from "@/utils/Alerts"
import { ref } from "vue"
import { Client, ClientError } from "./client"

const searchResult = ref<any>({})

function searchbyID(identifier: string) {
    Client().getJson('orders/search_by_accession_or_tracking_number', { 
        accession_number: identifier
    }).then((res: any) => {
        if (res.ok) {
            searchResult.value = res.data
            router.push('/order/view/'+identifier)
        } else {
            toastWarning(res.error)
        }
    }).catch((e) => {
        toastDanger(ClientError.GENERAL_ERROR)
        console.error(e)
    })
}

export default function Search() {
    return {
        searchResult,
        searchbyID
    }
}